<template>

    <div id="table">
      <div v-for="(n, i) in list" :key="i">
        <span class="c-xs c-n">{{n.Number}}</span>
        <span :style="{'background-color':n.Color}"></span>
        <span class="c-xl c-f">{{n.FirstName}} "{{n.NickName}}"</span>
        <span class="c-lg c-f">{{n.Team}}</span>
        <span class="c-xs c-f"><a href="#" @click="goTo(n.ID)"><img src="@/assets/time-icon.png" /></a></span>
        <span class="c-xs c-f"><a :href="n.SteamLink" target="_blank"><img src="@/assets/steam-icon.png" /></a></span>
      </div>
    </div>

</template>

<script>
import {getRows} from "@/context/functions.js"

export default {
  name: 'ListPilots', 
  data () { return {
    list: null,
    payload: "resource=pilots"
  }},
  methods: {
    getRows,
    goTo(id) {
      this.$emit("goTo", {
        screen: 3, 
        sendData: { id: id }
      });
    }
  },
  mounted() {
    this.getRows(this);
  }
}
</script>
