<template>

    <div id="table">
      <p>Piloto:</p>
      <div style="margin-bottom: 2rem;">
        <span class="c-xs c-n">{{list.info[0].Number}}</span>
        <span :style="{'background-color':list.info[0].Color}"></span>
        <span class="c-xl c-f">{{list.info[0].FirstName}} "{{list.info[0].NickName}}"</span>
        <span class="c-lg c-f">{{list.info[0].Team}}</span>
        <span class="c-xs c-f"><a :href="list.info[0].SteamLink" target="_blank"><img src="@/assets/steam-icon.png" /></a></span>
      </div>

      <p>Tempos Marcados:</p>
      <div v-for="(n, i) in list.times" :key="i">
        <span class="c-xs c-n">{{n.ID}}</span>
        <span class="c-lg c-f">{{n.Country}}</span>
        <span class="c-lg c-f">{{n.CircuitName}}</span>
        <span class="c-xs c-f">{{n.Difficult}}</span>
        <span class="c-md c-f">{{n.Times}}</span>
        <span class="c-md c-f">{{formatDate(n.Dates)}}</span>
      </div>
    </div>

</template>

<script>
import {getRows, formatDate} from "@/context/functions.js"

export default {
  name: 'PilotTimes', 
  props: {
    sendData: Object
  },
  data () { return {
    list: null,
    payload: "resource=pilot_times"
  }},
  methods: {
    getRows,
    formatDate
  },
  mounted() {
    this.payload = "resource=pilot_times&pilot=" + this.sendData.id;
    this.getRows(this);
  }
}
</script>
