<template>

    <div id="table">
      <span v-if="list">

        <p>Dados do Circuito:</p>
        <span class="c-xs c-n">{{list.info[0].ID}}</span>
        <span class="c-lg c-f">{{list.info[0].Country}}</span>
        <span class="c-lg c-f">{{list.info[0].CircuitName}}</span>
        <span class="c-xs c-f">{{list.info[0].Difficult}}</span>
        <span class="c-sm c-f">{{list.info[0].Distance}} km</span>

        <p  style="margin-top: 3rem;">Tempos Marcados:</p>
        <div v-for="(n, i) in list.times" :key="i">
          <span class="c-xs c-n">{{n.Number}}</span>
          <span :style="{'background-color':n.Color}"></span>
          <span class="c-xl c-f">{{n.FirstName}} "{{n.NickName}}"</span>
          <span class="c-lg c-f">{{n.Team}}</span>
          <span class="c-md c-f">{{n.Times}}</span>
          <span class="c-md c-f">{{formatDate(n.Dates)}}</span>
        </div>

      </span>
      <span v-if="!list">
        <p>Nenhum dado disponível</p>
      </span>
    </div>

</template>

<script>
import {getRows, formatDate} from "@/context/functions.js"

export default {
  name: 'PilotTimes', 
  props: {
    sendData: Object
  },
  data () { return {
    list: null,
    payload: "resource=circuit_times"
  }},
  methods: {
    getRows,
    formatDate
  },
  mounted() {
    this.payload = "resource=circuit_times&circuit=" + this.sendData.id;
    this.getRows(this);
  }
}
</script>
