<template>

    <div id="table">
      <div v-for="(n, i) in list" :key="i">
        <span class="c-xs c-n">{{n.OrderGP}}</span>
        <span :style="{'background-color':(n.Approved == 1) ? '#2CB23B' : '#444444'}"></span>
        <span class="c-lg c-f">{{n.Country}}</span>
        <span class="c-lg c-f">{{n.CircuitName}}</span>
        <span class="c-xs c-f">{{n.Difficult}}</span>
        <span class="c-sm c-f">{{n.Distance}} km</span>
        <span class="c-xs c-f"><a href="#" @click="goTo(n.ID)"><img src="@/assets/time-icon.png" /></a></span>
      </div>
    </div>

</template>

<script>
import {getRows} from "@/context/functions.js"

export default {
  name: 'ListCircuits', 
  data () { return {
    list: null,
    payload: "resource=circuits"
  }},
  methods: {
    getRows,
    goTo(id) {
      this.$emit("goTo", {
        screen: 4, 
        sendData: { id: id }
      });
    }
  },
  mounted() {
    this.getRows(this);
  }
}
</script>
