<template>
  <div id="app">
    <div class="left">
      <Sidebar @navigateTo="screen = $event"/>
    </div>
    <div class="right">
      <Header />

      <ListPilots v-if="screen == 0" @goTo="goTo($event)" />
      <ListCircuits v-if="screen == 1" @goTo="goTo($event)" />
      <ListTimeTrials v-if="screen == 2" @goTo="goTo($event)" />

      <PilotTimes v-if="screen == 3" :sendData="sendData" />
      <CircuitTimes v-if="screen == 4"  :sendData="sendData" />

    </div>
  </div>
</template>

<script>

// PENDENTE: TIME TRIAL / ALL TIMES

import Header from "./components/header.vue";
import Sidebar from "./components/sidebar.vue";

import ListPilots from './modules/ListPilots.vue';
import ListCircuits from './modules/ListCircuits.vue';
import ListTimeTrials from './modules/ListTimeTrials.vue';

import PilotTimes from './modules/PilotTimes.vue';
import CircuitTimes from './modules/CircuitTimes.vue';

export default {
  name: 'App',
  components: {
    Header,
    Sidebar,

    ListPilots,
    ListCircuits,
    ListTimeTrials,
    
    PilotTimes,
    CircuitTimes,
  },
  data () { return {
    screen: 0,
    sendData: {},
  }},
  methods: {
    goTo(data) {
      this.sendData = data.sendData; 
      this.screen = data.screen;
    }
  }
}
</script>
