import axios from 'axios'

const getRows = (content) => {

  const url = `https://f1academy.000webhostapp.com/?${content.payload}`
  axios.post(url)
    .then(res => {
      content.list = res.data;
    })
    .catch(error => {
        return ({status: 'error', message: `${error.data}`});
    });
}

const formatDate = (date) => {
  const newDate = new Date(date);
  return (
    ((newDate.getDate() <= 9) ? '0'+newDate.getDate() : newDate.getDate()) + "/" +
    (((newDate.getMonth()+1) <= 9) ? '0'+(newDate.getMonth()+1) : (newDate.getMonth()+1)) + "/" +
    newDate.getFullYear()
  );
}

export { getRows, formatDate }