<template>

    <div id="table">
      <div v-for="(n, i) in list" :key="i">
        <span class="c-xs c-n">{{n.OrderGP}}</span>
        <!-- <span :style="{'background-color':n.Color}"></span> -->
        <span class="c-lg c-f">{{n.Country}}</span>
        <span class="c-lg c-f">{{n.CircuitName}}</span>
        <span class="c-md c-f">{{formatDate(n.Dates)}}</span>
        <span class="c-xs c-f"><a href="#" @click="goTo(n.ID_Circuit)"><img src="@/assets/time-icon.png" /></a></span>
      </div>
    </div>

</template>

<script>
import {getRows, formatDate} from "@/context/functions.js"

export default {
  name: 'ListPilots', 
  data () { return {
    list: null,
    payload: "resource=tt_list"
  }},
  methods: {
    getRows,
    formatDate,
    goTo(id) {
      this.$emit("goTo", {
        screen: 4, 
        sendData: { id: id }
      });
    }
  },
  mounted() {
    this.getRows(this);
  }
}
</script>
